import http from "http";
import { WebSocketServer } from "ws";

const PORT = process.env.PORT || 3000;

let players = {};

// HTTP server (for the status page)
const server = http.createServer((req, res) => {
  res.writeHead(200, { "Content-Type": "text/html" });
  res.end(`
    <html>
      <head>
        <title>Blockplex Server</title>
        <style>
          body {
            background: #111;
            color: #0f0;
            font-family: monospace;
            text-align: center;
            padding-top: 100px;
          }
        </style>
      </head>
      <body>
        <h1>Blockplex Multiplayer Server</h1>
        <p>Status: Online</p>
        <h1>Blockplex Server</h1>
        <p>Players online: ${Object.keys(players).length}</p>
      </body>
    </html>
  `);
});

// WebSocket server
const wss = new WebSocketServer({ server });

wss.on("connection", (socket) => {
  const id = Math.random().toString(36).slice(2);
  players[id] = { x: 0, y: 0, z: 0, rot: 0 };

  // Send the ID to the client
  socket.send(JSON.stringify({ type: "id", id }));

  socket.on("message", (msg) => {
    try {
      const data = JSON.parse(msg);

      if (data.type === "update") {
        players[id] = data.state;
      }

      console.log("Received message from", id, ":", data);
    } catch {}
  });

  socket.on("close", () => {
    delete players[id];
  });
});

// Broadcast player list
setInterval(() => {
  const snapshot = JSON.stringify(players);
  wss.clients.forEach((client) => client.send(snapshot));
}, 33);

server.listen(PORT, () => {
  console.log("Listening on port", PORT);
});
